
.item .list-item {
  padding: 10px 10px 12px;
  border-bottom: 1px solid #d6d6d6;
  overflow: hidden;
}
.item .list-item .content .con-left {
    float: left;
}
.item .list-item .content .con-left img {
      max-width: 61px;
      max-height: 84px;
}
.item .list-item .content .con-center {
    float: left;
    margin-left: 5px;
}
.item .list-item .content .con-center .introduce {
      padding: 0 2px 4px;
}
.item .list-item .content .con-center .introduce .title {
        font-size: 16px;
        color: #000;
}
.item .list-item .content .con-center .introduce .move-tag {
        display: inline-block;
        padding: 0px 4px 2px;
        border-radius: 3px;
        background-color: #2895db;
        font-size: 11px;
        text-align: center;
        height: 14px;
        line-height: 17px;
        color: #fff;
        margin-left: 4px;
}
.item .list-item .content .con-center .describe {
      color: #666;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 15px;
}
.item .list-item .content .con-center .info {
      margin-top: 5px;
      font-size: 12px;
      padding: 3px 0;
      color: #666;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.item .list-item .content .con-right {
    float: right;
    text-align: right;
}
.item .list-item .content .con-right .number {
      display: block;
      color: #ff9a00;
      font-size: 18px;
      line-height: 24px;
      white-space: nowrap;
}
.item .list-item .content .con-right .ticket {
      display: block;
      font-size: 13px;
      margin-top: 20px;
      padding: 4px 17px;
      background-color: transparent;
      border-radius: 4px;
      border: 1px solid #e54847;
      color: #e54847;
      display: inline-block;
      vertical-align: top;
      white-space: nowrap;
}
.item .empty {
  height: 49px;
}



