
.bar {
  padding: 6px 10px 6px 7px;
  height: 38px;
  background: #df2d2d;
  overflow: hidden;
}
.bar .selec {
    float: left;
    margin: 6px 0 0 4px;
    padding: 4px;
    color: #FFF;
    font-size: 16px;
    line-height: 18px;
    border-radius: 2px;
}
.bar .selec .icon {
      float: right;
}
.bar .selec .icon img {
        width: 1.1rem;
        height: 1.1rem;
        margin: 1px 0 0 3px;
}
.bar .mine {
    float: right;
    width: 1.3rem;
    height: 1.3rem;
}
.bar .mine em img {
      color: #fff;
      margin: 8px 10px 0 0;
}



