
.movie-info .info-up {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
.movie-info .info-up .ell {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.movie-info .info-up .name-address {
    color: #333;
    line-height: 1.7;
}
.movie-info .info-up .introduce {
    color: gray;
    line-height: 1.7;
    font-size: 12px;
}
.movie-info .info-pic {
  overflow-x: scroll;
  padding: 0 10px;
}
.movie-info .info-pic .zone {
    overflow: hidden;
    width: 800px;
}
.movie-info .info-pic .zone .pic {
      float: left;
      margin: 15px 5px 10px 0;
}
.movie-info .info-pic .zone .pic a {
        display: inline-block;
}
.movie-info .info-pic .zone .pic a img {
          width: 65px;
          height: 90px;
}
.movie-info .info-title {
  padding: 10px;
  height: 29px;
  line-height: 29px;
  border-bottom: 1px solid #eee;
}
.movie-info .info-title .name {
    float: left;
    color: #333;
    font-size: 14px;
}
.movie-info .info-title .number {
    margin-left: 7px;
    color: #F07C0A;
    font-size: 14px;
}
.movie-info .info-data {
  padding: 10px 0;
  height: 26px;
  width: 100%;
  border: 0 solid #ccc;
  border-width: 1px 0;
  background: #efefef;
  position: relative;
}
.movie-info .info-data a {
    float: left;
    width: 100px;
    text-align: center;
    line-height: 26px;
    font-size: 16px;
    color: #666;
    text-decoration: none;
    padding: 0 8px;
    white-space: nowrap;
    border-radius: 13px;
    margin: 0 10px;
}
.movie-info .info-data a.active {
      color: #fff;
      background-color: #f00;
}
.movie-info .info-list table {
  width: 100%;
  height: 56px;
}
.movie-info .info-list table tbody tr {
    overflow: hidden;
    border-bottom: 1px solid #eee;
}
.movie-info .info-list table tbody tr td {
      float: left;
      text-align: center;
      vertical-align: middle;
      height: 56px;
}
.movie-info .info-list table tbody tr .time {
      color: #df2d2d;
      font-size: 20px;
      line-height: 22px;
      background: url("../../assets/img/iconright.png") 100% center no-repeat;
      background-size: 3px 49px;
      padding: 10px 0 0 15px;
}
.movie-info .info-list table tbody tr .time .start-time {
        display: block;
        color: #df2d2d;
        font-size: 20px;
        margin: 4px 15px 0 0;
}
.movie-info .info-list table tbody tr .time .end-time {
        display: block;
        font-size: 12px;
        color: #9a9a9a;
        font-style: normal;
        margin-right: 15px;
}
.movie-info .info-list table tbody tr .info {
      color: #df2d2d;
      font-size: 20px;
      line-height: 22px;
      padding: 10px 0 0 15px;
      margin-left: 30px;
}
.movie-info .info-list table tbody tr .info .info-e {
        max-width: 180px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 14px;
}
.movie-info .info-list table tbody tr .info .room {
        display: block;
        font-size: 12px;
        color: #9a9a9a;
        font-style: normal;
        margin-right: 15px;
}
.movie-info .info-list table tbody tr .price {
      color: #df2d2d;
      font-size: 20px;
      line-height: 45px;
      padding: 10px 0 0 15px;
      margin-left: 30px;
}
.movie-info .info-list table tbody tr .price .price-num {
        display: block;
        font-size: 24px;
        color: #9a9a9a;
        font-style: normal;
        margin-right: 15px;
        color: #f00;
}
.movie-info .info-list table tbody tr .zuowei {
      padding: 10px 0 0 15px;
      margin-left: 30px;
}
.movie-info .info-list table tbody tr .zuowei .zuowei-wrap {
        margin-top: 13px;
}
.movie-info .info-list table tbody tr .zuowei .zuowei-wrap a {
          font-size: 13px;
          background: #e64847;
          padding: 8px 10px;
          border-radius: 4px;
          color: #fff;
          white-space: nowrap;
}
.movie-info .empty {
  height: 49px;
}



